// @flow

import styled from 'styled-components/macro';
import {fluid, page, spacing, colors} from '../../config/styles';
import Button, {StyledButton} from '../button';
import Meta from './meta';
import Link from 'next/link';

export default function Card({
    title,
    author,
    date,
    excerpt,
    children,
    href,
    words
}: PostMeta & {children?: any}) {
    return (
        <Link href={href} passHref>
            <Container>
                <Title>{title}</Title>
                <Meta {...{author, date, words}} />
                {excerpt ? (
                    <Excerpt dangerouslySetInnerHTML={{__html: excerpt}} />
                ) : (
                    <Excerpt>{children}</Excerpt>
                )}
                <Button tabIndex={-1}>View post</Button>
            </Container>
        </Link>
    );
}

export const Cards = styled.div`
    width: 100%;
    ${page.post.withVar('width', 'max-width')};
    padding: ${spacing.medium} 0;
    margin-top: ${spacing.large};
    margin: 0 auto;
`;

const Title = styled.h2`
    ${fluid('font-size', '1.3rem', '1.5rem')};
    margin-top: 0;
    line-height: 1;
`;

const Excerpt = styled.p`
    margin-top: ${spacing.regular};
    line-height: 1.3;
    font-size: 1.1rem;
`;

const Container = styled.a.attrs({tabIndex: 0})`
    position: relative;
    display: flex;
    flex-flow: column nowrap;
    cursor: pointer;
    text-decoration: none;
    color: ${page.color};
    padding: ${spacing.medium};
    outline: none;

    &:not(:active):focus {
        ${colors.fmtVar('primary', 'inset 0 0 0 2px ??', 'box-shadow')};
        ${StyledButton} {
            transition: none;
            opacity: 0.8;
        }
    }

    & + & {
        margin-top: ${spacing.medium};
        padding-top: ${spacing.medium};
    }

    ${StyledButton} {
        margin-left: auto;
    }
`;
